import { useRef, useEffect } from 'react';
import { Container, Grid, Typography, Stack, Box} from '@mui/material';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import ItemDiv from "../components/ItemDiv.js";
import ImageWithSkeleton from '../components/ImageWithSkeleton.js';

gsap.registerPlugin(ScrollTrigger);

const clientStyles = {
    height: {md:110, sm: 120, xs: 196},
    width: {md:165, sm: 180, xs: 294},
    mt:{md:2, sm: 1, xs: 1},
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
}

const Clients = () => {
    
    const clientRef = useRef(null);
    const tl = useRef(null);
    
    useEffect(() => {
        
        let ctx = gsap.context(() => {

            tl.current = gsap.timeline({
                scrollTrigger: {
                    trigger: ".firstClients",
                    toggleActions: "restart pause resume none"
                }
            })
            
            tl.current.from(".firstClients", {opacity: 0, duration: 3})
            tl.current.from(".secondClients", {opacity: 0, duration: 3, delay: -2.7})
            tl.current.from(".thirdClients", {opacity: 0, duration: 3, delay: -2.7})

        }, clientRef);

        return () => ctx.revert();

    }, []);

    return (
        <Container component="section" ref={clientRef} sx={{pt:38}} maxWidth={"md"} fixed>
            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <Typography variant="h3" component="h4" align="center">Clients</Typography>
                </Grid>

                <Grid container direction="row" justifyContent="center" alignItems="center" item xs={12}>
                    <ItemDiv sx={{height:3, width:40, padding:0}} darkhex="#AE7EAA" lighthex="#ff97cc"/>
                </Grid>

                <Grid item xs={12}>
                    <Stack direction="row" justifyContent="center" alignItems="center" spacing={{md:2, sm: 1, xs: 1}}>
                        <Box className="thirdClients" sx={{display: {md:"inline", sm: "none", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/GLI.webp"} text={"GLI Logo"} width={120} height={80}/>
                            </ItemDiv>
                        </Box>
                        <Box className="secondClients" sx={{display: {md:"inline", sm: "inline", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/NTH.webp"} text={"Neon Treehouse Logo"} width={150} height={100}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/BTG.webp"} text={"Blind Tiger Logo"} width={150} height={100}/>
                            </ItemDiv>
                        </Box>
                        <Box className="firstClients">
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/LK4.webp"} text={"Link4 Logo"} width={105} height={70}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/KML.webp"} text={"Karmine Leather Logo"} width={135} height={90}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/AFL.webp"} text={"AFL Max Logo"} width={135} height={90}/>
                            </ItemDiv>
                        </Box>
                        <Box className="secondClients" sx={{display: {md:"inline", sm: "inline", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/VOC.webp"} text={"Vocablyfy Logo"} width={150} height={100}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/GDM.webp"} text={"GDay Meats Logo"} width={135} height={90}/>
                            </ItemDiv>
                        </Box>
                        <Box className="thirdClients" sx={{display: {md:"inline", sm: "none", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/WBG.webp"} text={"WBG Legal Logo"} width={150} height={100}/>
                            </ItemDiv>
                        </Box>
                    </Stack>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Clients